"use client";
import { useState, useEffect } from "react";
import Link from "next/link";
import { normalizePath } from "@/lib/utils";

async function getNews() {
    try {
        const res = await fetch('/web/api/news', { cache: 'no-store' });
        if (!res.ok) throw new Error('Failed to fetch');
        const data = await res.json();
        return data.news || [];
    } catch (e) {
        console.error("Failed to fetch news:", e);
        return [];
    }
}

export default function News() {
    const [news, setNews] = useState([]);
    const [currentFeatured, setCurrentFeatured] = useState(0);
    const [loading, setLoading] = useState(true);
    const [settings, setSettings] = useState({
        news_title: "ข่าวสารและกิจกรรมล่าสุด",
        news_description: "ติดตามความเคลื่อนไหวและบรรยากาศในรั้ววิทยาลัย"
    });

    useEffect(() => {
        const loadData = async () => {
            try {
                const [dbNews, settingsRes] = await Promise.all([
                    getNews(),
                    fetch('/web/api/news/settings').then(res => res.json())
                ]);

                // Default data if DB is empty
                const defaultNews = [
                    {
                        id: 1,
                        title: "พิธีรับประกาศนียบัตร ประจำปีการศึกษา 2566",
                        date: "15 พฤศจิกายน 2566",
                        description: "ขอแสดงความยินดีกับผู้สำเร็จการศึกษาทุกคนที่มุ่งมั่นตั้งใจ จนประสบความสำเร็จในวันนี้",
                        image: "https://images.unsplash.com/photo-1523580494863-6f30312248f5?q=80&w=1920&auto=format&fit=crop",
                        category: "Activity",
                        is_featured: true
                    }
                ];

                setNews(dbNews.length > 0 ? dbNews : defaultNews);

                if (settingsRes.success && settingsRes.settings) {
                    setSettings(settingsRes.settings);
                }
            } catch (err) {
                console.error("Load data error:", err);
            } finally {
                setLoading(false);
            }
        };

        loadData();
    }, []);

    // Split news into Featured and General
    const featuredNews = news.filter(item => item.is_featured);
    const generalNews = news.filter(item => !item.is_featured);

    // Filter fallback: If no featured, take first 3. If no general, take rest.
    const displayFeatured = featuredNews.length > 0 ? featuredNews : news.slice(0, 3);

    // On home page, only show up to 3 general news items
    const displayGeneral = generalNews.length > 0 ? generalNews.slice(0, 3) : news.slice(3, 6);

    const handlePrev = () => {
        setCurrentFeatured(prev => (prev === 0 ? displayFeatured.length - 1 : prev - 1));
    };

    const handleNext = () => {
        setCurrentFeatured(prev => (prev === displayFeatured.length - 1 ? 0 : prev + 1));
    };

    if (loading) return null; // Or a loading skeleton

    return (
        <section id="news" className="py-20 bg-white">
            <div className="max-w-7xl mx-auto px-6">
                {/* Header */}
                <div className="text-center mb-12 animate-fade-in-up">
                    <h2 className="text-3xl font-bold text-[#2b4a8a] mb-3">{settings.news_title}</h2>
                    <div className="w-20 h-1 bg-[#f2cc0d] mx-auto rounded-full"></div>
                    <p className="mt-4 text-slate-600 max-w-2xl mx-auto text-base">
                        {settings.news_description}
                    </p>
                </div>

                {/* Featured News - Large Card with Navigation */}
                {displayFeatured.length > 0 && (
                    <div className="mb-12 animate-fade-in-up animation-delay-200">
                        <Link href={`/news/${displayFeatured[currentFeatured].slug || displayFeatured[currentFeatured].id}`}>
                            <div className="relative rounded-2xl overflow-hidden shadow-xl group hover:shadow-2xl transition-all duration-500 cursor-pointer">
                                <div className="relative h-96 overflow-hidden">
                                    <img
                                        alt={displayFeatured[currentFeatured].title}
                                        className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-105"
                                        src={normalizePath(displayFeatured[currentFeatured].image)}
                                    />
                                    <div className="absolute inset-0 bg-gradient-to-t from-[#2b4a8a]/90 via-[#2b4a8a]/50 to-transparent"></div>
                                </div>

                                <div className="absolute bottom-0 left-0 right-0 p-8 text-white">
                                    <span className="inline-block px-3 py-1 bg-[#f2cc0d] text-[#2b4a8a] rounded-full text-sm font-bold mb-3">
                                        ข่าวเด่น
                                    </span>
                                    <h3 className="text-3xl font-bold mb-3 leading-tight">
                                        {displayFeatured[currentFeatured].title}
                                    </h3>
                                    <p className="text-slate-200 mb-4 text-base line-clamp-2">
                                        {displayFeatured[currentFeatured].description}
                                    </p>
                                    <div className="flex items-center justify-between">
                                        <div className="flex items-center gap-2 text-sm text-slate-300">
                                            <span className="material-icons text-base">event</span>
                                            <span>{displayFeatured[currentFeatured].date}</span>
                                        </div>

                                        {/* Navigation Buttons */}
                                        <div className="flex gap-3">
                                            <button
                                                onClick={handlePrev}
                                                className="w-10 h-10 rounded-full bg-white/10 backdrop-blur-sm border border-white/30 flex items-center justify-center hover:bg-[#f2cc0d] hover:text-[#2b4a8a] hover:border-[#f2cc0d] transition-all"
                                                aria-label="Previous news"
                                            >
                                                <span className="material-icons text-xl">chevron_left</span>
                                            </button>
                                            <button
                                                onClick={handleNext}
                                                className="w-10 h-10 rounded-full bg-white/10 backdrop-blur-sm border border-white/30 flex items-center justify-center hover:bg-[#f2cc0d] hover:text-[#2b4a8a] hover:border-[#f2cc0d] transition-all"
                                                aria-label="Next news"
                                            >
                                                <span className="material-icons text-xl">chevron_right</span>
                                            </button>
                                        </div>
                                    </div>

                                    {/* Indicators */}
                                    <div className="flex gap-2 mt-4">
                                        {displayFeatured.map((_, index) => (
                                            <button
                                                key={index}
                                                onClick={() => setCurrentFeatured(index)}
                                                className={`h-1 rounded-full transition-all ${index === currentFeatured
                                                    ? 'w-8 bg-[#f2cc0d]'
                                                    : 'w-4 bg-white/40 hover:bg-white/60'
                                                    }`}
                                                aria-label={`Go to news ${index + 1}`}
                                            />
                                        ))}
                                    </div>
                                </div>
                            </div>
                        </Link>
                    </div>
                )}

                {/* News Grid - Vertical Cards */}
                <div className="grid md:grid-cols-3 gap-8 animate-fade-in-up animation-delay-300">
                    {displayGeneral.map((item, index) => (
                        <Link
                            key={index}
                            href={`/news/${item.slug || item.id}`}
                            className="group bg-white rounded-2xl overflow-hidden shadow-lg hover:shadow-2xl transition-all duration-500 hover:-translate-y-2 cursor-pointer"
                        >
                            {/* Image */}
                            <div className="relative h-56 overflow-hidden">
                                <img
                                    alt={item.title}
                                    className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-110"
                                    src={normalizePath(item.image)}
                                />
                                <div className="absolute inset-0 bg-gradient-to-t from-black/50 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-500"></div>

                                {/* Category Badge */}
                                <div className="absolute top-4 left-4">
                                    <span className="px-3 py-1 bg-[#f2cc0d] text-[#2b4a8a] rounded-full text-xs font-bold">
                                        {item.category}
                                    </span>
                                </div>
                            </div>

                            {/* Content */}
                            <div className="p-6">
                                <div className="flex items-center gap-2 text-sm text-slate-500 mb-3">
                                    <span className="material-icons text-base">event</span>
                                    <span>{item.date}</span>
                                </div>

                                <h4 className="text-lg font-bold text-[#2b4a8a] mb-3 line-clamp-2 group-hover:text-[#f2cc0d] transition-colors leading-tight">
                                    {item.title}
                                </h4>

                                <p className="text-sm text-slate-600 mb-4 line-clamp-2 leading-relaxed">
                                    {item.description}
                                </p>

                                <button className="flex items-center gap-2 text-sm font-bold text-[#2b4a8a] group-hover:gap-3 transition-all">
                                    อ่านเพิ่มเติม
                                    <span className="material-icons text-base group-hover:translate-x-1 transition-transform">arrow_forward</span>
                                </button>
                            </div>
                        </Link>
                    ))}
                </div>

                {/* View All Button */}
                <div className="text-center mt-12">
                    <Link
                        href="/news"
                        className="bg-white border-2 border-[#2b4a8a] text-[#2b4a8a] hover:bg-[#2b4a8a] hover:text-white px-8 py-3 rounded-full font-bold transition-all shadow-lg hover:shadow-xl inline-flex items-center gap-2"
                    >
                        ดูข่าวสารทั้งหมด
                        <span className="material-icons text-base">east</span>
                    </Link>
                </div>
            </div>


        </section>
    );
}
