"use client";
import { useState, useEffect } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { normalizePath } from "@/lib/utils";

export default function Navbar() {
    const [menuOpen, setMenuOpen] = useState(false);
    const [cmsMenus, setCmsMenus] = useState([]);
    const [siteSettings, setSiteSettings] = useState({
        site_name: '',
        site_logo: 'school',
        site_logo_url: '',
        register_link: '#',
        register_button_text: 'สมัครเรียนเลย'
    });
    const pathname = usePathname();

    useEffect(() => {
        fetchMenus();
        fetchSettings();
    }, []);

    const fetchMenus = async () => {
        try {
            const res = await fetch('/web/api/menus');
            const data = await res.json();
            if (data.success && data.menus.length > 0) {
                setCmsMenus(data.menus);
            }
        } catch (err) {
            console.error("Failed to fetch menus:", err);
        }
    };

    const fetchSettings = async () => {
        try {
            const res = await fetch('/web/api/settings');
            const data = await res.json();
            if (data.success && data.settings) {
                setSiteSettings(data.settings);
            }
        } catch (error) {
            console.error("Failed to fetch settings:", error);
        }
    };

    const getLink = (menu) => {
        if (menu.page_id && menu.page_slug) {
            // Special cases for root-level physical routes
            const rootPages = ['personnel', 'courses', 'news'];
            if (rootPages.includes(menu.page_slug)) {
                return `/${menu.page_slug}`;
            }
            return `/page/${menu.page_slug}`;
        }
        if (menu.course_id) {
            const coursePath = menu.course_slug ? `/courses/${menu.course_slug}` : `/courses?id=${menu.course_id}`;
            return coursePath;
        }
        if (menu.news_id) {
            const newsPath = menu.news_slug ? `/news/${menu.news_slug}` : `/news?id=${menu.news_id}`;
            return newsPath;
        }

        // Handle hash links correctly when on internal pages
        if (menu.url && menu.url.startsWith('#') && pathname !== '/') {
            return `/${menu.url}`;
        }

        return menu.url || '#';
    };

    // Default menus if CMS is empty
    const defaultMenus = [
        { id: 'home', title: 'หน้าแรก', url: '/', children: [] },
        { id: 'courses', title: 'หลักสูตรที่เปิดสอน', url: '/courses', children: [] },
        { id: 'personnel', title: 'คณาจารย์', url: '/personnel', children: [] },
        { id: 'about', title: 'เกี่ยวกับเรา', url: '/#about', children: [] },
        { id: 'contact', title: 'ติดต่อสอบถาม', url: '/#contact', children: [] },
    ];

    const menusToRender = cmsMenus.length > 0 ? cmsMenus : defaultMenus;

    return (
        <nav className="fixed top-0 w-full z-50 bg-white/90 backdrop-blur-md border-b border-[#2b4a8a]/5">
            <div className="max-w-7xl mx-auto px-6 h-20 flex items-center justify-between gap-4">
                {/* Logo Area */}
                <Link href="/" className="flex items-center gap-3 shrink-0 group">
                    <div className="w-12 h-12 flex items-center justify-center overflow-hidden transition-transform group-hover:scale-105">
                        {siteSettings.site_logo_url ? (
                            <img src={normalizePath(siteSettings.site_logo_url)} alt="Logo" className="w-full h-full object-contain" />
                        ) : (
                            <span className="material-icons text-[#2b4a8a] text-3xl">{siteSettings.site_logo}</span>
                        )}
                    </div>
                    <div className="flex flex-col">
                        <span className="text-xl font-extrabold text-[#2b4a8a] tracking-tighter leading-none">
                            {siteSettings.site_name}
                        </span>
                        {siteSettings.site_subtitle && (
                            <span className="text-[10px] text-[#2b4a8a]/60 font-medium leading-tight mt-0.5 max-w-[200px] line-clamp-1">
                                {siteSettings.site_subtitle}
                            </span>
                        )}
                    </div>
                </Link>

                {/* Desktop Menu */}
                <div className="hidden lg:flex items-center justify-center flex-1">
                    <div className="flex items-center gap-1 xl:gap-2">
                        {menusToRender.map((menu) => (
                            <div key={menu.id} className="relative group shrink-0">
                                <Link
                                    href={getLink(menu)}
                                    className={`text-[#2b4a8a] font-bold text-[14px] xl:text-[15px] hover:text-[#f2cc0d] transition-colors flex items-center gap-0.5 px-2 xl:px-4 py-6 uppercase tracking-tight whitespace-nowrap`}
                                >
                                    {menu.title}
                                    {menu.children && menu.children.length > 0 && (
                                        <span className="material-icons text-sm opacity-50">expand_more</span>
                                    )}
                                </Link>

                                {/* Dropdown for Sub-menus */}
                                {menu.children && menu.children.length > 0 && (
                                    <div className="absolute top-full left-1/2 -translate-x-1/2 w-56 bg-white shadow-2xl rounded-xl border border-gray-100 overflow-hidden opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 transform translate-y-2 group-hover:translate-y-0">
                                        <div className="p-2">
                                            {menu.children.map((child) => (
                                                <Link
                                                    key={child.id}
                                                    href={getLink(child)}
                                                    className="block px-4 py-2.5 text-[13px] font-medium text-[#2b4a8a] hover:bg-[#f2cc0d]/10 hover:text-[#f2cc0d] rounded-lg last:border-0 transition-colors"
                                                >
                                                    {child.title}
                                                </Link>
                                            ))}
                                        </div>
                                    </div>
                                )}
                            </div>
                        ))}
                    </div>
                </div>

                {/* Mobile Toggle */}
                <div className="lg:hidden flex items-center">
                    <button
                        className="p-2 text-[#2b4a8a]"
                        onClick={() => setMenuOpen(!menuOpen)}
                    >
                        <span className="material-icons text-3xl">menu</span>
                    </button>
                </div>
            </div>

            {/* Mobile Menu Overlay */}
            {menuOpen && (
                <div className="fixed inset-0 z-40 bg-[#2b4a8a]/95 backdrop-blur-xl lg:hidden flex flex-col items-center justify-center p-6 text-center">
                    <button
                        onClick={() => setMenuOpen(false)}
                        className="absolute top-6 right-6 p-2 rounded-full bg-white/10 text-white hover:bg-white/20 transition-colors"
                    >
                        <span className="material-icons text-3xl">close</span>
                    </button>
                    <div className="flex flex-col items-center gap-6 w-full max-w-sm max-h-[80vh] overflow-y-auto pt-10">
                        {menusToRender.map((menu) => (
                            <div key={menu.id} className="w-full">
                                <Link
                                    onClick={() => !menu.children?.length && setMenuOpen(false)}
                                    className="text-2xl font-black text-white hover:text-[#f2cc0d] block uppercase tracking-tight"
                                    href={getLink(menu)}
                                >
                                    {menu.title}
                                </Link>
                                {menu.children && menu.children.length > 0 && (
                                    <div className="flex flex-col gap-3 mt-4 pl-4 border-l-2 border-white/10">
                                        {menu.children.map((child) => (
                                            <Link
                                                key={child.id}
                                                onClick={() => setMenuOpen(false)}
                                                className="text-lg font-medium text-white/70 hover:text-[#f2cc0d]"
                                                href={getLink(child)}
                                            >
                                                {child.title}
                                            </Link>
                                        ))}
                                    </div>
                                )}
                            </div>
                        ))}
                    </div>
                </div>
            )}
        </nav>
    );
}
